<template>
  <div class="index"></div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { useRoute } from "vue-router";
import { v4 as uuidv4 } from "uuid";
import {
  addFrontEndShareId,
  helpFrontEndShareId,
  getFrontEndShareIdInfo,
} from "../api/wxProject.js";
export default {
  name: "index",
  components: {},
  setup() {
    let route = useRoute();
    let query = route.query;
    const state = reactive({
      infoList: [],
    });

    onMounted(() => {
      init();
    });

    const init = async () => {
      window.document.title = "加载中...";
      let shareId = localStorage.getItem("shareId");
      let res;
      if (!shareId) {
        shareId = uuidv4();
        res = await methods.addFrontEndShareId(shareId);
      } else {
        res = await methods.getFrontEndShareIdInfo(shareId);

        if (!res.shareId) {
          localStorage.removeItem("shareId");
          init();
          return;
        }
      }
      let helpId = query.helpId;
      await methods.helpFrontEndShareId(shareId, helpId);
      window.location.href = "https://mp.weixin.qq.com/s/NXZfNceGfDG_G68rdb8u0g";
    };
    const methods = {
      addFrontEndShareId(shareId) {
        return new Promise(
          (resolve) => {
            var formData = new FormData();
            formData.append("shareId", shareId);
            addFrontEndShareId(
              formData,
              (res) => {},
              (res) => {
                localStorage.setItem("shareId", shareId);
                resolve(res);
              }
            );
          },
          (reject) => {}
        );
      },
      helpFrontEndShareId(shareId, helpId) {
        return new Promise(
          (resolve) => {
            var formData = new FormData();
            formData.append("shareId", shareId);
            formData.append("helpId", helpId);
            helpFrontEndShareId(
              formData,
              (res) => {},
              (res) => {
                resolve(res);
              }
            );
          },
          (reject) => {}
        );
      },
      getFrontEndShareIdInfo(shareId) {
        return new Promise(
          (resolve) => {
            getFrontEndShareIdInfo(
              {},
              (res) => {
                // console.log(res);
                throw new Error(res.toString());
              },
              (res) => {
                resolve(res);
              },
              {
                method: "get",
                params: {
                  shareId,
                },
              }
            );
          },
          (reject) => {
            reject();
          }
        );
      },
    };

    return {
      ...toRefs(state),
      ...methods,
    };
  },
};
</script>
